body{
  font-family: "Open Sans", serif;
  margin: 0;
}
.wrapper{
  background: linear-gradient(#4e54c8,#8f94fb);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-container{
  background-color: #1592B4;
  height: 90vh;
  padding-top: 30px;
  padding-left: 50px;
  padding-right: 50px;
  margin: auto;
}
.title{
  color: #fff;
  font-size: xx-large;
  font-weight: bold;
  text-align: center;
}
input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #fff;
  width: 100%;
  display: block;
  /* padding-bottom: 4px; */
  color: #fff !important;
  font-weight: lighter;
  /* letter-spacing: 2px; */
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}
input[type="text"] { 
  outline: none;
}

button {
border: 0;
padding: 8px 20px;
margin: 0 2px;
border-radius: 5px;
font-weight: lighter;
letter-spacing: 1px;
font-size: 15px;
cursor: pointer;
background-color: #000;
color: #fff;
font-weight: 100;
}

button:active {
outline: none;
}

.info {
width: 100%;
letter-spacing: 2px;
}

.info-key {
color: #fff;
border-bottom: solid 2px rgba(255,255,255,0.06);
font-weight: 400;
}

.info-key:last-child {
border: 0;
}

.info-val {
color: #fff;
font-weight: 200;
}